*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  background-color: #93f1d2;
  font-family: 'Roboto', sans-serif;
}
.main {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  /* width: 100vh; */
}
.center_box {
  width: 40%;
  background: #f4f4f4;
  height: auto;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0.5);
  border-radius: 15px;

}
.center_box h2 {

  background: transparent;
  background-color: greenyellow;
  color: #fff;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0.5);
  padding: 6px;
  margin-bottom: 15px;
  margin-top: 19px;
  font-size: 34px;
  text-align: center;
}
.divMain {
  padding: 10px 20px;
  background: rgb(199, 183, 183);
  margin-bottom: 15px;
  display: flex;
}
.divMain p {
  cursor: pointer;
  height: 30px;
  width: 30px;
  background: #fff;
  text-align: center;
  line-height: 31px;
  /* margin-left: 14px; */
  border-radius: 50%;
  display: flex;
  justify-content: center;
}
.divMain h3 {
  font-size: 17px;
  margin-left: 12px;
  display: flex;
  align-items: center;
}
p.answer {
  display: flex;
  padding: 20px;
}
.main1{
  background-color: #fff;
  height: 100vh;
}
.header{
  width: 100%;
  background-color: #f5ba13;
  line-height: 100px;
  display: flex;
  justify-content: start;
  padding-left: 50px;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0.5);
  align-items: center;
}
.header h1 {
  margin-left: 10px;
  color: #fff;
}
.main_note{
  width: 500px ;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
}
.my_note h1{
  color: #34495e;
}
.my_note p{
  color: gray;
}
form{
  min-height: 50px;
  color: #ECF0F1;
  padding: 15px 10px 5px 10px;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0.5);
  border-radius: 10px;
  position: relative;
}
input{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  margin: 10px 0px;
  font-weight: bold;
}
textarea{
  width: 100%;
  list-style: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
  margin: 10px 0px;
}
.MuiButton-root{
  width: 30px;
  height: 50px;
  position: absolute;
  bottom: -28px;
  left: 400px;
  cursor: pointer;
  background-color: #fff!important;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0.5);
  border-radius: 50%!important;
}
.MuiButton-root .plus_sign{
  font-size: 45px!important;
  font-weight: 900;
  color: #f4b400;
  cursor: pointer;
  z-index: 222;
  background: transparent;
}
.MuiButton-root :hover{
  background-color: #f4b400;
}
.MuiButton-root .plus_sign:hover{
  color:#333 ;
  background: transparent;
}
.note{
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  margin: 16px;
  width: 240px;
  margin: 16px;
  float: left;
}
.note h1{
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
}
.note .btn{
  position: relative;
  float: right;
  margin-right: 10px ;
  color: #f4b4f4; ;
  border: none;
  width: 50px;
  height: 50px;
  background: transparent;
  cursor: pointer;
  outline: none;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0.5);
  border-radius: 50%;
}
.deleteIcon{
  width: 0.7em;
  height: 0.7em;
}
.note .btn:hover{
  background-color: #f5ba13;
}
.note .btn:hover .deleteIcon{
  color: #fff;
}
footer{
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 10px;
  text-align: center;
  background-color: #fff;
}